    closeVideo() {
        let video: any = this.$refs.video;
        if(video){
            video.stopVideo();
        }
        this.showBox = '';
        setTimeout(()=>{
            this.showBox = 'close';
        },200)
    }

    startM(id: string) {
        this.showBox = 'video';
        let video: any = this.$refs.video;
        setTimeout(() => {
            video.playVideo(id);
        }, 1000)
    }


